body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.baseButton{
    border: none;
    border-radius: 0.4rem;
    padding: 0.5rem 1.5rem;
    margin-left: 1rem;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    color: white;
    background-color: skyblue;
}

.baseButton:hover{
    background-color: aquamarine;
    
}
.loginBlock{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgb(188, 228, 255);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.whiteBox{
    background: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 3rem;
}

.whiteBox form{
    display: flex;
    flex-direction: column;
}

.loginForm input{
    margin-bottom: 1rem;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
    line-height: 1.725rem;
}
.header{
    margin: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__searchInput{
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
    line-height: 1.9rem;
    width: auto;
    margin-right: -0.6rem;
}
.unloginIntro{
    margin: 1rem 15rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    line-height: 1.6rem;
}

.unloginIntro article{
    width: 100%;
    margin-bottom: 3rem;
    font-size: 1.1rem;
}

.articleHead {
    font-size: 2rem;
    font-weight: 800;
}

.mainWeather{
    display: flex;
    justify-content: space-around;
    background-color: rgb(195, 231, 255);
    margin: 1rem 5rem;
}

.mainWeather button{
    height: 250px;
    width: 250px;
    background-color: white;
    border: none;
    border-radius: 50%;
    margin: 1rem;
    font-size: 2.5rem;
    font-weight: 600;
}

.MyStyleList{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 350px;
    background-color: rgb(188, 228, 255);
    overflow: scroll;
    margin: 1rem 0rem;
}

.MyStyleList button{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
    padding: 1rem;
    border-radius: 3rem;
    border: none;
    font-size: 1.25rem;
}

.altImg{
    width: 200px;
    height: 200px;
    border: 1px solid black;
    background-color: beige;
}
.addCloth{
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    top: 0;
    background-color: rgb(188, 228, 255);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.addClothHeader{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.addClothHeader h1{
    font-size: 2rem;    
}

.addClothHeader img{
    height: 4rem;
}

.addClothBox{
    padding: 2rem;
    border-radius: 1rem;
    background-color: white;
}

.addClothForm{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}

.addClothFormInput{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 2rem;
}

.addClothFormLeft{
    display: flex;
    flex-direction: column;
    margin-right: 2rem;
}

.addClothFormLeft div{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.addClothFormRight{
    display: flex;
    flex-direction: column;
}

.addClothFormRight input{
    font-size: 1.3rem;
    line-height: 2rem;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
    margin-bottom: 0.4rem;
}

.addClothFormRight textarea{
}

.addClothFormButton{
    display: flex;
    justify-content: center;
}

.addClothSelect{
    margin-top: 1.125rem;
    height: 3rem;
}
